import * as React from 'react'
import { graphql, Link, StaticQuery } from 'gatsby'

import Page from '../components/Page'
import Container from '../components/Container'
import IndexLayout from '../layouts'

interface StaticQueryProps {
  site: {
    siteMetadata: {
      author: {
        name: string
        email: string
      }
    }
  }
}

const PageTwo = () => (
  <StaticQuery
    query={graphql`
      query PageTwoQuery {
        site {
          siteMetadata {
            author {
              name
              email
            }
          }
        }
      }
    `}
    render={(data: StaticQueryProps) => (
      <IndexLayout>
        <Page>
          <Container>
            <h1>Page 2</h1>
            <p>
              {data.site.siteMetadata.author.name + "'s email is "}
              <a href={'mailto:' + data.site.siteMetadata.author.email}>{data.site.siteMetadata.author.email}</a>
            </p>
            <ul>
              <li>
                <Link to="/a-markdown-page/">Show me some Markdown!</Link>
              </li>
              <li>
                <Link to="/">Take me back home.</Link>
              </li>
            </ul>
          </Container>
        </Page>
      </IndexLayout>
    )}
  />
)

export default PageTwo
